.ant {
  border: 1px solid;
  padding: 1rem;
  margin: 1rem;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -webkit-animation: fade-in 0.25s ease-in-out;
          animation: fade-in 0.25s ease-in-out;
}
.ant > * {
  margin: 0.5rem auto;
}

@media screen and (min-width: 768px) {
  .ant {
    padding: 1rem 2rem;
    width: auto;
    min-width: 16rem;
  }
}


